import React, {useEffect, useState} from 'react';
import Slider from "react-slick";
import {NavLink} from "react-router-dom";
import axios from "axios";
import {API_PATH_RU} from "../../tools/constants";



const PriceProvider = () => {

    const [pop ,setPop] = useState([])


    useEffect(() => {



        axios.get(API_PATH_RU + "device/v1/product/popular/")
            .then(res => {
                setPop(res.data)
            })
            .catch(err =>{
                console.log(err)
            })

    }, [])




    const settings = {

        className: "center",
        infinite: true,
        centerPadding: "60px",
        slidesToShow: 4.94,
        swipeToSlide: true,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 1
                }
            },
            {
                breakpoint: 992,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1
                }
            },
            {
                breakpoint: 550,
                settings: "unslick"
            }

        ]


    }


    return (
        <div className="position-relative">
        <div className="provider container">

            <h1 className="font-family-medium">Популярные</h1>
            <Slider {...settings}>


                {
                    pop.map(item => (
                        <div className="popular">
                            <div className="popular-child">
                                <div className="card-body">
                                    <img src="images/icon/stock .png" alt=""/>
                                    <span><img src={item.images[0]?.get_img_url} alt=""/></span>
                                    <h4>{item.category_name}</h4>
                                    <p>{item.main_content}
                                    </p>

                                    <h3  className="font-family-medium"><del></del><br/>{item.price}</h3>
                                    <div className="d-flex justify-content-center mt-3"><NavLink to="/equipment">
                                        <button type="button" className="btn btn-primary   font-family-medium" >Подробнее</button>
                                    </NavLink></div>                        </div>
                            </div>
                        </div>
                    ))
                }
            </Slider>
        </div>
            <div className="line32">
                <div className="line-insert">
                    <svg width="704" height="101" viewBox="0 0 704 101" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M853.1 1H731.997L693.803 38.2624H454.702L435.062 18.622H262.18L236.685 42.0764H5.11438"
                              stroke="#0086E7" stroke-width="1.5" stroke-miterlimit="10"></path>
                        <path
                            d="M5.3461 46.4236C2.93957 46.4236 0.998837 44.4828 0.998837 42.0763C0.998837 39.6698 2.93957 37.729 5.3461 37.729C7.75263 37.729 9.69336 39.6698 9.69336 42.0763C9.69336 44.4828 7.75263 46.4236 5.3461 46.4236Z"
                            fill="#0086E7"></path>
                        <path
                            d="M1118.1 99.4238H996.997L958.803 62.1614H515.14L495.5 81.8018H383.5L358.005 58.3474H270.114"
                            stroke="#0086E7" stroke-width="1.5" stroke-miterlimit="10"></path>
                        <path
                            d="M270.346 54.0003C267.94 54.0003 265.999 55.941 265.999 58.3475C265.999 60.7541 267.94 62.6948 270.346 62.6948C272.753 62.6948 274.693 60.7541 274.693 58.3475C274.693 55.941 272.753 54.0003 270.346 54.0003Z"
                            fill="#0086E7"></path>
                        <g opacity="0.4">
                            <path
                                d="M782.66 42.2625L661.557 42.2624L623.363 5.00003L386.098 5.00001L364.499 36.8026L252.5 36.8026L227.005 13.3482L139.114 13.3482"
                                stroke="#0086E7" stroke-width="1.5" stroke-miterlimit="10"></path>
                            <path
                                d="M139.346 9.001C136.94 9.001 134.999 10.9417 134.999 13.3483C134.999 15.7548 136.94 17.6956 139.346 17.6956C141.753 17.6956 143.693 15.7548 143.693 13.3483C143.693 10.9417 141.753 9.001 139.346 9.001Z"
                                fill="#0086E7"></path>
                        </g>
                    </svg>
                </div>
            </div>
            <div className="line33">
                <div className="line-insert">
                    <svg width="445" height="47" viewBox="0 0 445 47" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M-408 1L-286.897 1.00001L-248.703 38.2624L-9.60279 38.2624L10.0376 18.6221L182.92 18.6221L208.415 42.0765L439.985 42.0765"
                            stroke="#0086E7" stroke-width="1.5" stroke-miterlimit="10"></path>
                        <path
                            d="M439.754 46.4238C442.16 46.4238 444.101 44.4831 444.101 42.0765C444.101 39.67 442.16 37.7292 439.754 37.7292C437.347 37.7292 435.406 39.67 435.406 42.0765C435.406 44.4831 437.347 46.4238 439.754 46.4238Z"
                            fill="#0086E7"></path>
                        <g opacity="0.4">
                            <path
                                d="M-337.561 42.2625L-216.458 42.2625L-178.264 5.00005L59.0013 5.00005L80.6006 36.8027L192.6 36.8027L218.095 13.3483L305.985 13.3483"
                                stroke="#0086E7" stroke-width="1.5" stroke-miterlimit="10"></path>
                            <path
                                d="M305.754 9.001C308.16 9.001 310.101 10.9417 310.101 13.3483C310.101 15.7548 308.16 17.6956 305.754 17.6956C303.347 17.6956 301.406 15.7548 301.406 13.3483C301.406 10.9417 303.347 9.001 305.754 9.001Z"
                                fill="#0086E7"></path>
                        </g>
                    </svg>
                </div>
            </div>
        </div>
    );
};

export default PriceProvider;