import React, {useEffect, useState} from 'react';
import Slider from "react-slick";
import {NavLink} from "react-router-dom";
import axios from "axios";
import {API_PATH_RU} from "../../tools/constants";

const EquipmentNew = () => {
    const settings = {
        className: "center",
        infinite: true,
        centerPadding: "60px",
        slidesToShow: 4.94,
        swipeToSlide: true,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 1
                }
            },
            {
                breakpoint: 992,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1
                }
            },
            {
                breakpoint: 550,
                settings: "unslick"
            }

        ]
        // afterChange: function (index) {
        //     console.log(
        //         `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
        //     );
        // }

    }



    const [pop ,setPop] = useState([])


    useEffect(() => {



        axios.get(API_PATH_RU + "device/v1/product/new/")
            .then(res => {
                setPop(res.data)
            })
            .catch(err =>{
                console.log(err)
            })

    }, [])


    return (
        <div>
            <div id="new23" className="provider container">

                <h1>Новинки</h1>
                <Slider {...settings}>
                    {
                        pop.map(item => (
                            <div className="popular">
                                <div className="popular-child">
                                    <div className="card-body">
                                        <img src="images/icon/stock .png" alt=""/>
                                        <span><img src={item.images[0]?.get_img_url} alt=""/></span>
                                        <h4>{item.category_name}</h4>
                                        <p>{item.main_content}
                                        </p>

                                        <h3  className="font-family-medium"><del></del><br/>{item.price}</h3>
                                        <div className="d-flex justify-content-center mt-3"><NavLink to="/equipment">
                                            <button type="button" className="btn btn-primary   font-family-medium" >Подробнее</button>
                                        </NavLink></div>                        </div>
                                </div>
                            </div>
                        ))
                    }
                </Slider>
            </div>
        </div>
    );
};

export default EquipmentNew;